.la-notification {
  position: fixed;
  color: white;
  font-size: 12px;
  left: 8px;
  top: 8px;
  right: 8px;
}
.la-notification .content {
  padding: 8px 25px 8px 10px;
  position: absolute;
  left: 0;
  right: 0;
  opacity: 0;
  color: inherit;
  text-decoration: none;
  visibility: hidden;
  -webkit-transition: all .6s;
  -o-transition: all .6s;
  transition: all .6s;
  border-radius: 3px;
  text-shadow: 0 0 3px rgba(1, 1, 1, 0.3);
  background-color: transparent;
  line-height: 150%;
}
.la-notification.active .content.warn {
  background-color: #ff9800;
}
.la-notification.active .content.error {
  background-color: #e51c23;
}
.la-notification.active .content.default {
  background-color: #3f51b5;
}
.la-notification.active .content.success {
  background-color: #8bc34a;
}
.la-notification.active .content.dark {
  background-color: #414141;
}
.la-notification.active .content {
  opacity: 1;
  visibility: visible;
}
.la-notification .close {
  position: absolute;
  right: 8px;
  top: 10px;
  display: inline-block;
  width: 14px;
  height: 14px;
  overflow: hidden;
  cursor: pointer;
}
.la-notification .close::before,
.la-notification .close::after {
  content: '';
  position: absolute;
  height: 1px;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
  background: #fff;
}
.la-notification .close::before {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.la-notification .close::after {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}



